.JSAnimation {
	width: 125px;
	height: 125px;
	position: flex;

	transform-origin: center;
	transform: translate(100%, 100%);
}
.js-animation-title {
	font-size: 4em;
	display: flex;
	text-align: center;
	margin: auto;
}
.js-animation-line {
	font-size: 4em;
	margin-top: -80px;
	overflow: hidden;
	display: flex;
	font-weight: 600;
	width: 0px;
}
.js-animation-instruction {
	width: 100px;
}

.move-line {
	-webkit-animation-name: js-line;
	        animation-name: js-line;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.default-position {
	-webkit-animation-name: js-default-position;
	        animation-name: js-default-position;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes js-line {
	0% {
		width: 0px;
	}
	100% {
		width: 60px;
	}
}

@keyframes js-line {
	0% {
		width: 0px;
	}
	100% {
		width: 60px;
	}
}

@-webkit-keyframes js-default-position {
	0% {
	}
	100% {
		transform: rotate(0deg) translate(100%, 100%);
	}
}

@keyframes js-default-position {
	0% {
	}
	100% {
		transform: rotate(0deg) translate(100%, 100%);
	}
}

.CSSAnimation {
	width: 125px;
	color: yellow;
	-webkit-animation-name: move;
	        animation-name: move;
	-webkit-animation-duration: 4s;
	        animation-duration: 4s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	position: flex;
	transform-origin: center;
	transform: translate(80%, 150%);
}
.css-animation-title {
	font-size: 4em;
	display: flex;
	text-align: center;
	margin: auto;
}
.css-animation-line {
	font-size: 4em;
	margin-top: -80px;
	overflow: hidden;
	display: flex;
	font-weight: 600;

	-webkit-animation-name: line;

	        animation-name: line;
	-webkit-animation-duration: 4s;
	        animation-duration: 4s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

@-webkit-keyframes line {
	0% {
		width: 0px;
	}
	10% {
		width: 0px;
	}
	30% {
		width: 125px;
	}
	100% {
		width: 125px;
	}
}

@keyframes line {
	0% {
		width: 0px;
	}
	10% {
		width: 0px;
	}
	30% {
		width: 125px;
	}
	100% {
		width: 125px;
	}
}

@-webkit-keyframes move {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 1;
	}
	30% {
		transform: rotate(0deg) translate(80%, 150%);
	}
	55% {
		transform: rotate(-25deg) translate(50%, 200%);
	}
	100% {
		transform: rotate(-25deg) translate(250%, 350%);
	}
}

@keyframes move {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 1;
	}
	30% {
		transform: rotate(0deg) translate(80%, 150%);
	}
	55% {
		transform: rotate(-25deg) translate(50%, 200%);
	}
	100% {
		transform: rotate(-25deg) translate(250%, 350%);
	}
}

#svg-animation-svg {
	width: 300px;
	display: block;
