.animate-letter-s {
	stroke-dasharray: 290;
	stroke-dashoffset: 290;
	-webkit-animation: dash1 5s linear infinite;
	        animation: dash1 5s linear infinite;
}
@-webkit-keyframes dash1 {
	to {
		stroke-dashoffset: 0;
	}
}
@keyframes dash1 {
	to {
		stroke-dashoffset: 0;
	}
}
.animate-letter-v {
	stroke-dasharray: 15;
	stroke-dashoffset: 350;
	-webkit-animation: dash2 10s linear alternate infinite;
	        animation: dash2 10s linear alternate infinite;
}
@-webkit-keyframes dash2 {
	to {
		stroke-dashoffset: 0;
	}
}
@keyframes dash2 {
	to {
		stroke-dashoffset: 0;
	}
}
