.background {
	margin: auto;
	margin-top: 0;
}
.time-line-text {
	padding-top: 800px;
	margin-left: 30px;
	max-width: 30%;
}

.time-line-left-column {
	float: left;
	margin-left: 20%;
}

.time-line-right-column {
	float: right;
	margin-right: 20%;
}

/* RESPONSIVE */

/* Tablet */
@media only screen and (max-width: 1024px) {
	.time-line-left-column {
		margin-left: 5%;
	}
	.time-line-right-column {
		margin-right: 5%;
	}
}

/* Phone */
@media only screen and (max-width: 479px) {
	.background-svg {
		display: none;
	}
	.background-svg-mobile {
		visibility: visible;
	}
	.time-line-left-column {
		margin-left: 0;
	}
	.time-line-right-column {
		margin-right: 0;
	}
}

.background-svg {
	position: absolute;
	z-index: -100;
	overflow: hidden;
	left: 0;
	top: 0;
}
.background-svg-mobile {
	position: absolute;
	z-index: -100;
	overflow: hidden;
	left: 0;
	top: 0;
	visibility: hidden;
}

@media only screen and (max-width: 479px) {
	.background-svg {
		visibility: hidden;
	}
	.background-svg-mobile {
		visibility: visible;
	}
}
